@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Quicksand:wght@500;600&display=swap');

*{
  margin: 0;
  font-family: "Quicksand";
  color: black;
}
.home{
  background-color: pink;
}

.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1{
  color: #f1356d;
}
.navbar .links{
  margin-left: auto;
}
.navbar a{
  margin-left: 16px;
  text-decoration: none;
  padding:6px ;
}
.navbar a:hover{
  color:#f1356d;
}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.blog-preview{
  padding:10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid wheat;
}
.blog-preview:hover{
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
   color: #f1356d;
   margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
}
.blog-details h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}
.blog-details  div{
  margin: 20px 0;
}
.blog-details button{
  background-color: #f1356d;
  color: #ddd;
  border-radius: 8px;
  border: 0;
  padding: 8px;
  cursor: pointer;
}
.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea , .create select{
  width: 100%;
  padding: 6px 10px;
  margin:  10px 0;
  border:  1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button{
  background-color: #f1356d;
  color: #f2f2f2;
  border: 0 ;
  padding: 8px;
  border-radius: 8px;
  cursor:pointer;
}